import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController, ViewController, ToastController } from 'ionic-angular';
import { AngularFirestore } from '@angular/fire/firestore';
import { Collections } from '../../app/app.models';

@IonicPage()
@Component({
  selector: 'page-pay-template-edit',
  templateUrl: 'pay-template-edit.html',
})
export class PayTemplateEditPage {

  public template: any = {};

  constructor(
    public modal: ModalController,
    public view: ViewController,
    private afs: AngularFirestore,
    private toast: ToastController,
    private navParams: NavParams,
  ) {
  }

  ionViewDidLoad() {
    this.template = this.navParams.get('template');
  }

  async editTemplate() {

    const id = this.template.id;
    await this.afs.collection(Collections.PayTemplate).doc(id).update({
      ...this.template,
      id,
    });

    this.toast.create({ message: 'Costo editado correctamente', duration: 4000 }).present();
    this.view.dismiss();

  }

}



